import { Routes } from '@angular/router';
import { AdminLayoutComponent } from './shared/components/layouts/admin-layout/admin-layout.component';
import { AuthLayoutComponent } from './shared/components/layouts/auth-layout/auth-layout.component';
import { AuthGuard } from './shared/guards/auth.guard';

export const rootRouterConfig: Routes = [
  
  {
    path: '',
    redirectTo: '/others/Service',
    pathMatch: 'full'
  },
  
  {
    path: '',
    component: AuthLayoutComponent,
    children: [
      {
        path: 'sessions',
        loadChildren: () => import('./views/sessions/sessions.module').then(m => m.SessionsModule),
        data: { preload: true }
      }
    ]
  }, {
    path: '',
    component: AdminLayoutComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'others',
        loadChildren: () => import('./views/others/others.module').then(m => m.OthersModule),
        data: { preload: false }
      }, {
        path: 'home',
        loadChildren: () => import('./views/home/home.module').then(m => m.HomeModule),
        data: { preload: false, title: 'Home', breadcrumb: 'Home' }
      },
      {
        path: 'document',
        loadChildren: () => import('./views/document-upload-from-email/document-upload-from-email.module').then(m => m.DocumentUploadFromEmailModule),
        data: { preload: false, title: 'Document', breadcrumb: 'Document' }
      }, 
      {
        path: 'loan',
        loadChildren: () => import('./views/loan-account/loan-account.module').then(m => m.LoanAccountModule),
        data: { preload: false, title: 'Loan Account', breadcrumb: 'Create Loan Account' }
      }, {
        path: 'video-verification',
        loadChildren: () => import('./views/others/video-verification/video-verification.module').then(m => m.VideoVerificationModule),
      }, {
        path: 'onboardingCorporate',
        loadChildren: () => import('./views/others/onboarding-corporate/onboarding-corporate.module').then(m => m.OnboardingCorporateModule),
        data: { preload: false }
      }, {
        path: 'tasks',
        loadChildren: () => import('./views/tasks/tasks.module').then(m => m.TasksModule),
        data: { preload: false }
      }, {
        path: 'cardaccount',
        loadChildren: () => import('./views/card-account/card-account.module').then(m => m.CardAccountModule),
        // data: { title: 'Others', breadcrumb: 'OTHERS'}
      }

    ]
  },
  {
    path: '**',
    redirectTo: 'sessions/404'
  }
];

